import React from "react";
import withStyles from "@material-ui/core/styles/withStyles";
import Typography from "@material-ui/core/Typography";
import {
  Add, Refresh, FormatAlignRight, Filter, MoreHoriz
} from "@material-ui/icons";
import {Link} from "react-router-dom";
import Toolbar from "@material-ui/core/Toolbar";
import Button from "@material-ui/core/Button";
import AppContext from "../../AppContext"
import {Checkbox, Table, TableBody,Chip,
  Avatar,TableCell, TableHead, TableRow, AppBar, ButtonBase, IconButton, Menu,
  LinearProgress,
  MenuList,MenuItem} from "@material-ui/core";
import PageAppBar from "../../components/ActivityPrimaryAppBar";
import axios from 'axios';
import AppToolBar from "../../components/AppToolBar"
import DataSource from "../../DataSource"

let styles = {
  ProductItemRoot: {
    padding: 8
  },
  toolbar: {
    display: "flex",
    justifyContent: "space-between"
  }
};


class TableProductsView extends React.Component {
  dataSource;
  constructor(props){
    super(props)

  }
  state = {
    loading: true,
    loaded: false,
    selected: [],
    orders: [
    ]
  };
  selectAll = (event, checked) => {
    if (checked) {
      this.setState(state => {
        state.selected= state.orders.map(v=> v._id)
        return state;
      });
    } else {
      this.setState(state => {
        state.selected= []
        return state;
      });
    }
  };

  selectSingle = itemKey => {
    return (event, checked) => {
      this.setState(state => {
        if(checked){
          //push
          state.selected.push(itemKey)
        }else{
          //Pull out
          state.selected= state.selected.filter(v=> v!=itemKey)
        }
        return state;
      });
    };
  };

  componentWillMount(){
    this.dataSource= new DataSource(this.context.user.token, this.context.user.store._id)
    this.dataSource.getStoreCategories().then(v=>{
      this.setState({loading: false})
      let orders= v.data.items
      this.setState({orders: orders})
    }).catch(v=> { this.setState({loading: false})})
  }
  static contextType= AppContext

  render() {
    let { classes } = this.props;
    let selectedCategoriesOptionToolBar = (
        <React.Fragment>
          <AppToolBar>
            <div>
              <Button variant={"flat"} color={"inherit"}>Delete</Button>
            </div>
          </AppToolBar>
        </React.Fragment>
    );

    let defaultToolbar = (
        <React.Fragment>
          <AppToolBar>
            <Typography>
              Store categories
            </Typography>
            <div style={{display:"flex"}}>
              <div style={{padding:"0px 8px"}}>
                <IconButton>
                  <Refresh/>
                </IconButton>
              </div>
              <div style={{padding:"0px 8px"}}>
                <ButtonBase style={{padding:"12px"}}
                            to={"/customer/new"}
                            component={Link}>
                  <Add/>
                  <Typography color={"inherit"}> CREATE</Typography>
                </ButtonBase>
              </div>
            </div>
          </AppToolBar>
        </React.Fragment>
    );

    let ordersAvailable = (
        <React.Fragment>
          <div style={{ overflow: "hidden", background:"ghostwhite" }} >
            {this.state.selected.length !== 0
                ? selectedCategoriesOptionToolBar
                : defaultToolbar}
            <Table style={{ padding: 16, overflowX: "scroll", minWidth: 300 }}>
              <TableHead>
                <TableRow>
                  <TableCell >
                    <Checkbox onChange={this.selectAll} />
                  </TableCell>
                  <TableCell>Customer</TableCell>
                  <TableCell>OrderID</TableCell>
                  <TableCell>Date</TableCell>
                  <TableCell>items</TableCell>
                  <TableCell>status</TableCell>
                  <TableCell>total</TableCell>
                  <TableCell>Action</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {this.state.orders.map((v, i) => (
                    <TableRow>
                      <TableCell>
                        <Checkbox
                            checked={this.state.selected.some(v2=> v2 == v._id)}
                            onChange={this.selectSingle(v._id)}
                        />
                      </TableCell>
                      <TableCell>{new Date(v.created+"").getUTCDate()}</TableCell>
                      <TableCell><Avatar src={"data:image/png;base64,"}/></TableCell>
                      <TableCell>{v.title}</TableCell>
                      <TableCell><IconButton component={Link} to={`/customers/${v._id}`}><MoreHoriz/></IconButton></TableCell>
                    </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        </React.Fragment>
    );
    let ordersNotAvailable = (
        <div>
          <Typography>
            You dont have any products yet, click the button above to add some.
          </Typography>
        </div>
    );
    return (
        <React.Fragment>
          {this.state.loading? <LinearProgress/> :
              <div>
                {this.state.orders.length == 0
                    ? ordersNotAvailable
                    : ordersAvailable}
              </div>
          }

        </React.Fragment>
    );
  }
}
let WithStylesTableProductsView = withStyles(styles)(TableProductsView);
class Orders extends React.Component {
  state = {
    ui: {
      create: {
        anchorEl: null
      }
    }
  };
  handleCreateButtonOpen = event => {
    event.persist();
    this.setState(state => {
      state.ui.create.anchorEl = event.currentTarget;
      return state;
    });
  };
  handleCreateButtonClose = () => {
    this.setState(state => {
      state.ui.create.anchorEl = null;
      return state;
    });
  };

  static contextType= AppContext
  render() {
    let anchorEl = this.state.ui.create.anchorEl;
    let open = Boolean(anchorEl);
    let { classes, theme } = this.props;
    return (
        <React.Fragment>
          <PageAppBar>
            <div>
              <Typography variant={"title"}>Sales</Typography>
            </div>
            <div>
              <ButtonBase style={{padding:"12px", color:"black"}}
                          to={"/order/:order"}
                          component={Link}>
                <Add/>
                <Typography color={""}> CREATE</Typography>
              </ButtonBase>
            </div>
          </PageAppBar>
          <div style={{ padding:"24px 24px" }}>
            <TableProductsView />
          </div>
        </React.Fragment>
    );
  }
}

export default withStyles(styles)(Orders)
