import React from "react";
import withStyles from "@material-ui/core/styles/withStyles";
import Typography from "@material-ui/core/Typography";
import {
  Add, Refresh, FormatAlignRight, Filter, MoreHoriz, Delete, Edit, Collections as Group
} from "@material-ui/icons";
import {Link} from "react-router-dom";
import Toolbar from "@material-ui/core/Toolbar";
import Button from "@material-ui/core/Button";
import AppContext from "../../AppContext"
import {Checkbox, Table, TableBody,Chip,
  Avatar,TableCell, TableHead, TableRow, AppBar, ButtonBase, IconButton, Menu,
    LinearProgress,
  MenuList,MenuItem} from "@material-ui/core";
import PageAppBar from "../../components/ActivityPrimaryAppBar";
import AppToolBar from "../../components/AppToolBar"
import DataSource from "../../DataSource"
import NewCustomer from "../../components/NewCustomer"
let styles = {
  ProductItemRoot: {
    padding: 8
  },
  toolbar: {
    display: "flex",
    justifyContent: "space-between"
  }
};

class TableProductsView extends React.Component {
  dataSource;
  constructor(props){
    super(props)

  }
  state = {
    createCustomerDrawer: false,
    loading: true,
    loaded: false,
    selected: [],
    customers: [
    ]
  };
  selectAll = (event, checked) => {
    if (checked) {
      this.setState(state => {
        state.selected= state.customers.map(v=> v._id)
        return state;
      });
    } else {
      this.setState(state => {
        state.selected= []
        return state;
      });
    }
  };
  selectSingle = itemKey => {
    return (event, checked) => {
      this.setState(state => {
        if(checked){
          //push
          state.selected.push(itemKey)
        }else{
          //Pull out
          state.selected= state.selected.filter(v=> v!=itemKey)
        }
        return state;
      });
    };
  };

  loadCustomers(){
    this.dataSource.getStoreCustomers().then(v=>{
      this.setState({loading: false})
      let customers= v.data.items
      customers.forEach(async (v, i , a)=>{
        let customer= v.customer
       let maguser= await this.dataSource.getUser(customer)
        a[i].user= maguser
      })
      this.setState({customers: customers})
    }).catch(v=> {console.log(v); this.setState({loading: false})})

  }

  componentWillMount(){
    this.dataSource= new DataSource(this.context.user.token, this.context.user.store._id)
    this.loadCustomers()
  }
  static contextType= AppContext


  render() {
    let { classes } = this.props;
    let selectedCategoriesOptionToolBar = (
        <React.Fragment>
          <AppToolBar>
            <div>
              <IconButton><Delete/></IconButton>
              <IconButton><Edit/></IconButton>
              <IconButton><Group/></IconButton>
            </div>
            <div>
            </div>
          </AppToolBar>
        </React.Fragment>
    );

    let defaultToolbar = (
        <React.Fragment>
          <AppToolBar>
            <Typography>
              Store categories
            </Typography>
            <div style={{display:"flex"}}>
              <div style={{padding:"0px 8px"}}>
                <IconButton>
                  <Refresh/>
                </IconButton>
              </div>
              <div style={{padding:"0px 8px"}}>
                <ButtonBase style={{padding:"12px"}}
                            to={"/categories/new"}
                            component={Link}>
                  <Add/>
                  <Typography color={"inherit"}> CREATE</Typography>
                </ButtonBase>
              </div>
            </div>
          </AppToolBar>
        </React.Fragment>
    );

    let customersAvailable = (
        <React.Fragment>
          <div style={{ overflow: "hidden", background:"ghostwhite" }} >
            {this.state.selected.length !== 0
                ? selectedCategoriesOptionToolBar
                : defaultToolbar}
            <Table style={{ padding: 16, overflowX: "scroll", minWidth: 300 }}>
              <TableHead>
                <TableRow>
                  <TableCell >
                    <Checkbox onChange={this.selectAll} />
                  </TableCell>
                  <TableCell>Image</TableCell>
                  <TableCell>User Name</TableCell>
                  <TableCell>Group</TableCell>
                  <TableCell> Notification</TableCell>
                  <TableCell>Action</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {this.state.customers.map((v, i) => (
                    <TableRow>
                      <TableCell>
                        <Checkbox
                            checked={this.state.selected.some(v2=> v2 == v._id)}
                            onChange={this.selectSingle(v._id)}
                        />
                      </TableCell>
                      <TableCell><Avatar src={"data:image/png;base64,"}/></TableCell>
                      <TableCell>{v.customer}</TableCell>
                      <TableCell>{"General"}</TableCell>
                      <TableCell>None</TableCell>
                      <TableCell><IconButton component={Link} to={`/customers/${v._id}`}><MoreHoriz/></IconButton></TableCell>
                    </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        </React.Fragment>
    );


    let customersNotAvailable = (
        <div>
          <Typography>
            You dont have any products yet, click the button above to add some.
          </Typography>
        </div>
    );
    return (
        <React.Fragment>
          {this.state.loading? <LinearProgress/> :
              <div>
            {this.state.customers.length == 0
                ? customersNotAvailable
                : customersAvailable}
          </div>
          }
        </React.Fragment>
    );
  }
}
let WithStylesTableProductsView = withStyles(styles)(TableProductsView);


class Products extends React.Component {
  state = {
    addedCustomer: false,
    ui: {
      create: {
        anchorEl: null
      }
    }
  };
  static contextType= AppContext
  finishNewCustomer= (check)=>{
    if(check){
      this.setState({addedCustomer: true})
    }else{
    }
    this.setState({createCustomerDrawer: false})

  }
  openCreateCustomer= ()=>{
    this.setState({createCustomerDrawer: true})
  }
  render() {
    let anchorEl = this.state.ui.create.anchorEl;
    let open = Boolean(anchorEl);
    let { classes, theme } = this.props;
    return (
        <React.Fragment>
          {this.state.createCustomerDrawer? <NewCustomer open= {this.state.createCustomerDrawer} onFinish={this.finishNewCustomer}/>: null}
          <PageAppBar>
            <div>
              <Typography variant={"title"}>Customers</Typography>
            </div>
            <div>
              <ButtonBase style={{padding:"12px", color:"black"}} onClick={this.openCreateCustomer}>
                <Add/>
                <Typography color={""}> CREATE</Typography>
              </ButtonBase>
            </div>
          </PageAppBar>
          <div style={{ padding:"24px 24px" }}>
            <TableProductsView reloadCustomers/>
          </div>
        </React.Fragment>
    );
  }
}

export default withStyles(styles)(Products)
