import React from "react";
import withStyles from "@material-ui/core/styles/withStyles";
import Typography from "@material-ui/core/Typography";
import {
  Add, Refresh, FormatAlignRight, Filter, MoreHoriz,
    Delete, Collections as Group, Edit
} from "@material-ui/icons";
import {Link} from "react-router-dom";
import Toolbar from "@material-ui/core/Toolbar";
import Button from "@material-ui/core/Button";
import AppContext from "../../AppContext"
import {
  Checkbox, Table, TableBody, Chip,InputBase,Paper,
  Avatar, TableCell, TableHead, TableRow, AppBar, ButtonBase, IconButton, Menu,
  MenuList, MenuItem, LinearProgress
} from "@material-ui/core";
import PageAppBar from "../../components/ActivityPrimaryAppBar";
import axios from 'axios';
import AppToolBar from "../../components/AppToolBar"
import DataSource from "../../DataSource"

let styles = {
  ProductItemRoot: {
    padding: 8
  },
  toolbar: {
    display: "flex",
    justifyContent: "space-between"
  }
};

class TableProductsView extends React.Component {
  dataSource;
  constructor(props){
    super(props)

  }
  state = {
    loading: false,
    loaded: false,
    selected: [],
    products: [

    ]
  };
  selectAll = (event, checked) => {
    if (checked) {
      this.setState(state => {
        state.selected= state.products.map(v=> v._id)
        return state;
      });
    } else {
      this.setState(state => {
        state.selected= []
        return state;
      });
    }
  };

  selectSingle = itemKey => {
    return (event, checked) => {
      this.setState(state => {
        if(checked){
          //push
          state.selected.push(itemKey)
        }else{
          //Pull out
          state.selected= state.selected.filter(v=> v!=itemKey)
        }
        return state;
      });
    };
  };

  componentWillMount(){
    this.dataSource= new DataSource(this.context.user.token, this.context.user.store._id)
    this.dataSource.getStoreProducts().then(v=>{
      let products= v.data.items
      this.setState({products: products, loading:false})
    }).catch(v=> console.log(v))
    this.setState({loading: true})
  }
  static contextType= AppContext

  render() {
    let { classes } = this.props;
    let selectedProductOptionToolBar = (
      <React.Fragment>
        <AppToolBar>
          <div>
            <IconButton><Delete/></IconButton>
            <IconButton><Edit/></IconButton>
            <IconButton><Group/></IconButton>
          </div>
          <div>
          </div>
        </AppToolBar>
      </React.Fragment>
    );

    let defaultToolbar = (
      <React.Fragment>
        <AppToolBar>
            <Typography>
              Products
            </Typography>
            <div style={{width:"80%"}}>
              <InputBase fullWidth style={{background: "ghostwhite", padding: 8}}/>
            </div>
            <div style={{display:"flex"}}>
              <div style={{padding:"0px 8px"}}>
              <ButtonBase style={{padding:"12px"}}
                          to={"/products/product"}
                          component={Link}>
                <Add/>
                <Typography color={"inherit"}> CREATE</Typography>
              </ButtonBase>
              </div>
            </div>
        </AppToolBar>
      </React.Fragment>
    );


    let productsAvailable = (
      <React.Fragment>
        <Paper style={{ overflow: "hidden", background:"ghostwhite" }} >
          {this.state.selected.length !== 0
            ? selectedProductOptionToolBar
            : defaultToolbar}
          <Table style={{ padding: 16, overflowX: "scroll", minWidth: 300 }}>
            <TableHead>
              <TableRow>
                <TableCell >
                  <Checkbox onChange={this.selectAll} />
                </TableCell>
                <TableCell>Date</TableCell>
                <TableCell>Image</TableCell>
                <TableCell>title</TableCell>
                <TableCell>category</TableCell>
                <TableCell>price</TableCell>
                <TableCell>Action</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {this.state.products.map((v, i) => (
                <TableRow>
                  <TableCell>
                    <Checkbox
                      checked={this.state.selected.some(v2=> v2 == v._id)}
                      onChange={this.selectSingle(v._id)}
                    />
                  </TableCell>
                  <TableCell>{new Date(v.created+"").getUTCDate()}</TableCell>
                  <TableCell><Avatar src={"data:image/png;base64,"}/></TableCell>
                  <TableCell>{v.title}</TableCell>
                  <TableCell><div>
                    <Chip label={v.categories[0]} />
                  </div></TableCell>
                  <TableCell>{v.sellingPrice}</TableCell>
                  <TableCell><IconButton component={Link} to={`/products/${v._id}`}><MoreHoriz/></IconButton></TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </Paper>
      </React.Fragment>
    );
    let productsNotAvailable = (
      <div>
        <Typography align={"center"}>
          You dont have any products yet, click the button above to add some.
        </Typography>
      </div>
    );
    return (
      <React.Fragment>
        {this.state.loading? <LinearProgress/> :
            <div>
              {this.state.products.length == 0
                  ? productsNotAvailable
                  : productsAvailable}
            </div>
        }

      </React.Fragment>
    );
  }
}
let WithStylesTableProductsView = withStyles(styles)(TableProductsView);
class Products extends React.Component {
  state = {
    ui: {
      create: {
        anchorEl: null
      }
    }
  };
  handleCreateButtonOpen = event => {
    event.persist();
    this.setState(state => {
      state.ui.create.anchorEl = event.currentTarget;
      return state;
    });
  };
  handleCreateButtonClose = () => {
    this.setState(state => {
      state.ui.create.anchorEl = null;
      return state;
    });
  };

  static contextType= AppContext
  render() {
    let anchorEl = this.state.ui.create.anchorEl;
    let open = Boolean(anchorEl);
    let { classes, theme } = this.props;
    console.log("from products")
    console.log( this.context)
    return (
      <React.Fragment>
                <PageAppBar>
                  <div>
                    <Typography variant={"title"}> Products</Typography>
                  </div>
                  <div>
                    <ButtonBase style={{padding:"12px", color:"black"}}
                                to={"/products/new"}
                                component={Link}>
                      <Add/>
                      <Typography color={""}> CREATE</Typography>
                    </ButtonBase>
                  </div>
                </PageAppBar>
                <div style={{ padding:"24px 24px" }}>
                  <TableProductsView />
                </div>
      </React.Fragment>
    );
  }
}


export default withStyles(styles)(Products)
